import { Tab, Tabs } from "react-bootstrap";
import styled from "styled-components";
import {
  BillingModelTypes,
  BMTrialTypes,
  fromTokenDecimals,
  isAnySingleBM,
  isDynamicBM,
  isRecurringWithTrial,
} from "../../utils";
import { useEffect, useState } from "react";
import { ButtonPrimary } from "../Button";
import { useTranslation } from "react-i18next";
import { useSupportedTokens } from "../../state/supported-tokens/hooks";
import ConfigContent from "./ConfigContent";
import humanizeDuration from "humanize-duration";
import { DEFAULT_PERIOD_VALUE, UNIT_MEASURES } from "../SupportedLifeCyle";
import moment from "moment";

const IS_CONFIG_FEAT_ENABLED = true;

const WidgetContent = styled.div`
   {
    .nav-link {
      color: #5c5c5c;
      pointer-events: all !important;
    }
    .tab-content > div {
      width: 75%;
    }
    .code-copy {
      float: left;
      #CopyButtonPrimary {
        border-color: #7023ff;
        margin-right: 10px;
        background: #7023ff;
        color: #fff;
        width: 120px;
        display: inline-block;
        padding: 8px;
        border-radius: 10px;
      }
    }
    .notice-box {
      box-shadow: 2px 5px 10px 2px #4a435926;
      border-radius: 15px;
      padding: 30px 30px 80px;
      text-align: center;
      margin-bottom: 20px;
      &.Single,
      &.SingleDynamic {
        height: 270px;
      }
      &.Recurring {
        height: 340px;
      }
      &.RecurringFreeTrial,
      &.RecurringPaidTrial {
        height: 360px;
      }
      .connect-btn {
        background: #7023ff;
        border: 0;
        padding: 12px 40px;
        border-radius: 10px;
        color: #fff;
        font-size: 14px;
      }
      p.power {
        font-size: 9px;
        color: #b2acbf;
      }
    }

    .billing-model-details {
      margin-bottom: 1rem;
      .billing-model-merchant-name {
        > a {
          text-decoration: none;
        }
      }
      .billing-model-title {
        ::-webkit-scrollbar {
          display: none;
        }
        white-space: nowrap;
        overflow: scroll;
        text-overflow: "...";
      }
      .billing-model-price {
        font-size: 2rem;
      }
      .billing-model-duration {
        font-size: 0.8rem;
        margin-bottom: 2rem;
        color: #5c5c5c;
      }
    }
    .code-wrap {
      padding: 8px;
      width: 100%;
      height: 236px;
      overflow-x: auto;
      overflow-y: auto;
      /* white-space: nowrap; */
      /* overflow-x: hidden; */
      background-color: #000;
      color: #fff;
      text-align: left;
      line-height: 25px;
      margin: 4px 4px 20px;
      .body {
        color: #e6db74;
        font-size: 15px;
        line-height: 2;
        padding-left: 20px;

        span.attribute {
          color: #a6e22e;
        }
        input {
          background: #242424;
          width: 310px;
        }
      }
    }

    .frequency,
    .trial-period-frequency {
      #bm-trial-period-frequency,
      #bm-frequency-period {
        width: 110px;
        background: black;
      }
      display: flex;
      flex-wrap: wrap;
    }

    #bm-trial-period {
      width: 150px;
    }
    #bm-trial-amount {
      width: 200px;
    }
  }
`;

export interface MerchantWidgetContainerProps {
  billingModelType: BillingModelTypes;
  billingModelDetails: any;
  billingModelId?: string | null;
  isPreview?: boolean;
}

const MerchantWidgetContainer = ({
  billingModelType,
  billingModelId,
  billingModelDetails,
  isPreview = false,
}: MerchantWidgetContainerProps) => {
  const { t } = useTranslation();
  const supportedTokens = useSupportedTokens();
  // console.log('billingModelType: ', billingModelType);
  // console.log("billingModelDetails: ", billingModelDetails);

  const [copyWidgetUrl, setCopyWidgetUrl] = useState(false);
  const [codeSnippet, setCodeSnippet] = useState("");

  const [widget_billing_name, setWidgetBillingName] = useState("");
  const [widget_billing_price, setWidgetBillingPrice] = useState(0);
  const [widget_number_of_payment, setWidgetNumberOfPayment] = useState(0);
  const [widget_trial_period, setWidgetTrialPeriod] = useState(0);
  const [widget_frequency, setWidgetFrequency] = useState(0);
  const [widget_initial_amount, setWidgetInitialAmount] = useState(0);
  const [widget_token_currency, setWidgetTokenCurrency] = useState({
    address: "0xc5d495EEaA84942095b769342bC71125721692Da",
    symbol: "PMA",
    name: "PumaPay",
    decimals: 18,
  });
  const [durationPeriod, setDurationPeriod] = useState(DEFAULT_PERIOD_VALUE);
  const [trailPeriodDuration, setTrailPeriodDuration] =
    useState(DEFAULT_PERIOD_VALUE);

  const [widget_merchantName, setMerchantName] = useState("");
  const [widget_merchantUrl, setMerchantUrl] = useState("");
  const widgetData = {
    bmName: {
      value: widget_billing_name,
      setter: setWidgetBillingName,
    },
    bmPrice: {
      value: widget_billing_price,
      setter: setWidgetBillingPrice,
    },
    bmNoOfPayments: {
      value: widget_number_of_payment,
      setter: setWidgetNumberOfPayment,
    },
    bmTrialPeriod: {
      value: widget_trial_period,
      setter: setWidgetTrialPeriod,
    },
    bmFrequency: {
      value: widget_frequency,
      setter: setWidgetFrequency,
    },
    bmInitialAmount: {
      value: widget_initial_amount,
      setter: setWidgetInitialAmount,
    },
    bmCurrency: {
      value: widget_token_currency,
      setter: setWidgetTokenCurrency,
    },
    bmDurationPeriod: {
      value: durationPeriod,
      setter: setDurationPeriod,
    },
    bmTrialDuration: {
      value: trailPeriodDuration,
      setter: setTrailPeriodDuration,
    },
  };

  useEffect(() => {
    setMerchantName(billingModelDetails?.merchantName);
    setMerchantUrl(billingModelDetails?.merchantUrl);
  }, [billingModelDetails]);

  const BillingModelTypeForCodeSnippet = {
    [BillingModelTypes.Recurring]: "recurringPullPayment",
    [BillingModelTypes.RecurringFreeTrial]: "recurringPullPaymentWithFreeTrial",
    [BillingModelTypes.RecurringPaidTrial]: "recurringPullPaymentWithPaidTrial",
    [BillingModelTypes.RecurringDynamic]: "recurringDynamicPullPayment",
    [BillingModelTypes.Single]: "singlePullPayment",
    [BillingModelTypes.SingleDynamic]: "singleDynamicPullPayment",
  };

  useEffect(() => {
    if (!billingModelType) return;
    let template = `
        <!-- !! Please DO NOT Update OR Edit the BillingModelId and BillingModelType !! -->
        <!-- !! NOTE: The UniqueReference can be used so that the merchant can specify
            a unique string to so that this subscription can be identified.
            If the merchant does not specify UniqueReference, one will be created on the smart contract -->`;

    switch (billingModelType) {
      case String(BillingModelTypes.Single):
      case String(BillingModelTypes.RecurringPaidTrial):
      case String(BillingModelTypes.RecurringFreeTrial):
      case String(BillingModelTypes.Recurring):
        template += `<PumaPayWidget
                        BillingModelId="${billingModelId}"
                        BillingModelType="${BillingModelTypeForCodeSnippet[billingModelType]}"
                        UniqueReference=""
                      ></PumaPayWidget>`;
        break;
      case String(BillingModelTypes.SingleDynamic):
        template += `<PumaPayWidget
                      BillingModelId="${billingModelId}"
                      BillingModelType="${BillingModelTypeForCodeSnippet[billingModelType]}"
                      UniqueReference=""
                      BillingModelName="${widget_billing_name}"
                      SellingPrice="${widget_billing_price}"
                      SellingToken="${widget_token_currency?.address}"
                    ></PumaPayWidget>`;
        break;
      case String(BillingModelTypes.RecurringDynamic):
        template += `<PumaPayWidget
                        BillingModelId="${billingModelId}"
                        BillingModelType="${
                          BillingModelTypeForCodeSnippet[billingModelType]
                        }"
                        UniqueReference=""
                        BillingModelName="${widget_billing_name}"
                        SellingPrice="${widget_billing_price}"
                        SellingToken="${widget_token_currency?.address}"
                        NumberOfPayments="${widget_number_of_payment}"
                        Frequency="${
                          widget_frequency * durationPeriod.periodInSeconds
                        }"
                        ${
                          billingModelDetails?.trialType !==
                          BMTrialTypes.NoTrial
                            ? `TrialPeriod="${
                                widget_trial_period *
                                trailPeriodDuration.periodInSeconds
                              }"`
                            : ""
                        }
                        ${
                          billingModelDetails?.trialType ===
                          BMTrialTypes.PaidTrial
                            ? `InitialAmount="${widget_initial_amount}"`
                            : ""
                        }
                      >
                    </PumaPayWidget>`;
        break;
      default:
        console.error("Invalid billing model type!!");
        return;
    }
    template += `\n<script src="https://assets.pumapay.io/widget${
      process.env.REACT_APP_ENV === "TEST" ? "-dev/" : "/"
    }widgetScript.js"></script>`;
    setCodeSnippet(template);
  }, [
    widget_billing_name,
    widget_billing_price,
    widget_number_of_payment,
    widget_trial_period,
    widget_frequency,
    widget_initial_amount,
    widget_token_currency,
    durationPeriod,
    trailPeriodDuration,
    billingModelType,
    billingModelId,
  ]);

  const CopyWidgetPreviewUrl = () => {
    navigator.clipboard.writeText(codeSnippet).then(
      () => {
        // console.log("Copying to clipboard was successful!");
        setCopyWidgetUrl(true);
        setTimeout(() => {
          setCopyWidgetUrl(false);
        }, 3000);
      },
      (err) => {
        console.error("Could not copy text: " + err);
      }
    );
  };
  return (
    <WidgetContent>
      <Tabs
        defaultActiveKey="widget"
        id="uncontrolled-tab-example"
        className="mb-3"
      >
        <Tab eventKey="widget" title="Widget">
          <p>{t("widgetDescription")}</p>
          <p>{t("widgetNote")}</p>
          <div className={`notice-box ${BillingModelTypes[billingModelType]}`}>
            <div className="billing-model-details">
              <h4 className="billing-model-title">
                {isDynamicBM(BillingModelTypes[billingModelType])
                  ? widget_billing_name !== ""
                    ? widget_billing_name
                    : "{Billing Model Name}"
                  : billingModelDetails?.name}
              </h4>
              <div className="billing-model-merchant-name">
                <a
                  href={
                    widget_merchantUrl
                      ? widget_merchantUrl
                      : billingModelDetails?.merchantUrl
                  }
                  rel="noreferrer"
                  target={"_blank"}
                >
                  {isDynamicBM(BillingModelTypes[billingModelType])
                    ? widget_merchantName !== ""
                      ? widget_merchantName
                      : "{Merchant Name}"
                    : billingModelDetails?.merchantName}
                </a>
              </div>
              <div className="billing-model-price">
                {isDynamicBM(BillingModelTypes[billingModelType]) ? (
                  widget_billing_price !== 0 ? (
                    `${widget_billing_price} ${widget_token_currency?.symbol}`
                  ) : (
                    "{000} {XXX}"
                  )
                ) : (
                  <>
                    {billingModelDetails?.amount /
                      10 **
                        supportedTokens?.[billingModelDetails?.token]?.decimals}
                    &nbsp;
                    {supportedTokens?.[billingModelDetails?.token]?.symbol}
                  </>
                )}
              </div>
              <div className="billing-model-duration">
                {isAnySingleBM(BillingModelTypes[billingModelType]) ? (
                  "One time payment"
                ) : (
                  <>
                    {`You will be charged every ${
                      isDynamicBM(BillingModelTypes[billingModelType])
                        ? widget_frequency && widget_frequency !== 0
                          ? humanizeDuration(
                              widget_frequency *
                                durationPeriod?.periodInSeconds,
                              {
                                largest: 1,
                                round: true,
                                unitMeasures: UNIT_MEASURES,
                              }
                            )
                          : "{XXX}"
                        : humanizeDuration(billingModelDetails?.frequency, {
                            largest: 1,
                            round: true,
                            unitMeasures: UNIT_MEASURES,
                          })
                    }`}
                    <br />
                    {`until ${
                      isDynamicBM(BillingModelTypes[billingModelType])
                        ? widget_frequency &&
                          widget_number_of_payment &&
                          widget_frequency !== 0 &&
                          widget_number_of_payment !== 0
                          ? moment(
                              moment.now() +
                                widget_frequency *
                                  durationPeriod.periodInSeconds *
                                  widget_number_of_payment *
                                  1000
                            ).format("ll")
                          : "{XXX}"
                        : moment(
                            moment.now() +
                              billingModelDetails?.numberOfPayments *
                                billingModelDetails?.frequency *
                                1000
                          ).format("ll")
                    }`}
                    <br />
                    {`(${
                      isDynamicBM(BillingModelTypes[billingModelType])
                        ? widget_number_of_payment &&
                          widget_number_of_payment !== 0
                          ? widget_number_of_payment
                          : "{XXX}"
                        : billingModelDetails?.numberOfPayments
                    } charges in total)`}
                    {(isDynamicBM(BillingModelTypes[billingModelType]) ||
                      isRecurringWithTrial(
                        BillingModelTypes[billingModelType]
                      )) && (
                      <>
                        <br />
                        {widget_trial_period === BMTrialTypes.NoTrial ||
                        billingModelDetails?.trialType === BMTrialTypes.NoTrial
                          ? ""
                          : BillingModelTypes[billingModelType] ===
                              BillingModelTypes.RecurringFreeTrial ||
                            billingModelDetails?.trialType ===
                              BMTrialTypes.FreeTrial ||
                            widget_trial_period === BMTrialTypes.FreeTrial
                          ? "You have a free trial"
                          : `You have a trial (${
                              isDynamicBM(BillingModelTypes[billingModelType])
                                ? widget_initial_amount
                                  ? widget_initial_amount
                                  : "{XXX}"
                                : fromTokenDecimals(
                                    billingModelDetails?.initialAmount.toString(),
                                    supportedTokens?.[
                                      billingModelDetails?.token
                                    ]?.decimals.toString()
                                  )
                            } ${
                              supportedTokens?.[billingModelDetails?.token]
                                ?.symbol
                                ? supportedTokens?.[billingModelDetails?.token]
                                    ?.symbol
                                : widget_token_currency.symbol
                            })`}
                        {widget_trial_period === BMTrialTypes.NoTrial ||
                        billingModelDetails?.trialType === BMTrialTypes.NoTrial
                          ? ""
                          : " of "}
                        {isDynamicBM(BillingModelTypes[billingModelType])
                          ? widget_trial_period && widget_trial_period !== 0
                            ? ` ${widget_trial_period} ${trailPeriodDuration.label}`
                            : widget_trial_period === BMTrialTypes.NoTrial ||
                              billingModelDetails?.trialType ===
                                BMTrialTypes.NoTrial
                            ? ""
                            : "{XXX}"
                          : humanizeDuration(billingModelDetails?.trialPeriod, {
                              largest: 1,
                              round: true,
                              unitMeasures: UNIT_MEASURES,
                            })}
                      </>
                    )}
                    <br />
                    <br />
                    Note: You can cancel your subscription at any time
                  </>
                )}
              </div>
            </div>
            <button className="connect-btn">Connect Wallet</button>
            <p className="power">Powered by PumaPay v3.0</p>
          </div>
        </Tab>
        {!isPreview && (
          <Tab
            eventKey="code"
            title="Code"
            tabClassName={isPreview ? "disabled" : ""}
            disabled={isPreview}
          >
            <p>Copy the code snippet and add it to your website</p>
            <div className="code-wrap">
              <pre>
                <code>
                  <span className="tags">&lt;PumaPayWidget</span>
                  <div className="body">
                    <span className="attribute">BillingModelId</span>=
                    {`"${billingModelId}"`}
                    <br />
                    <span className="attribute">BillingModelType</span>=
                    {`"${BillingModelTypeForCodeSnippet[billingModelType]}"`}
                    <br />
                    <span className="attribute">UniqueReference</span>={`""`}
                    <br />
                    {isDynamicBM(BillingModelTypes[billingModelType]) && (
                      <>
                        <span className="attribute">BillingModelName</span>=
                        {`"${widget_billing_name}"`}
                        <br />
                        <span className="attribute">SellingPrice</span>=
                        {`"${widget_billing_price}"`}
                        <br />
                        <span className="attribute">SellingToken</span>=
                        {`"${widget_token_currency?.address}"`}
                        <br />
                        {BillingModelTypes[billingModelType] ===
                          BillingModelTypes.RecurringDynamic && (
                          <>
                            <span className="attribute">NumberOfPayments</span>=
                            {`"${widget_number_of_payment}"`}
                            <br />
                            <span className="attribute">Frequency</span>=
                            {`"${
                              widget_frequency * durationPeriod.periodInSeconds
                            }"`}
                            <br />
                            {billingModelDetails?.trialType !==
                              BMTrialTypes.NoTrial && (
                              <>
                                <span className="attribute">TrialPeriod</span>=
                                {`"${
                                  widget_trial_period *
                                  trailPeriodDuration.periodInSeconds
                                }"`}
                                <br />
                              </>
                            )}
                            {billingModelDetails?.trialType ==
                              BMTrialTypes.PaidTrial && (
                              <>
                                <span className="attribute">InitialAmount</span>
                                ={`"${widget_initial_amount}"`}
                                <br />
                              </>
                            )}
                          </>
                        )}
                      </>
                    )}
                  </div>
                  &gt;
                  <span className="tags">&lt;/PumaPayWidget&gt; </span>
                  <br />
                  <span className="tags">&lt;script</span>
                  <div className="body">
                    <span className="attribute">src</span>
                    ="https://assets.pumapay.io/widget
                    {process.env.REACT_APP_ENV === "TEST" ? "-dev/" : "/"}
                    widgetScript.js"
                  </div>
                  &gt;
                  <span className="tags">&lt;/script&gt;</span>
                </code>
              </pre>
            </div>
          </Tab>
        )}
        {isDynamicBM(BillingModelTypes[billingModelType]) &&
          !isPreview &&
          IS_CONFIG_FEAT_ENABLED && (
            <Tab
              eventKey="config"
              title="Configuration"
              tabClassName={isPreview ? "disabled" : ""}
              disabled={isPreview}
            >
              <p>You can configure the dynamic fields of your billing model </p>
              <ConfigContent
                billingModelId={billingModelId}
                billingModelType={billingModelType}
                billingModelDetails={billingModelDetails}
                widgetData={widgetData}
              />
            </Tab>
          )}
        {!isPreview && (
          <Tab
            eventKey="css"
            title="CSS"
            tabClassName={isPreview ? "disabled" : ""}
            disabled={isPreview}
          >
            <p>
              You will be able to customise the look and feel of the widget.
            </p>
            <div style={{ width: "100%", textAlign: "center" }}>
              Coming Soon!
            </div>
          </Tab>
        )}
      </Tabs>

      {!isPreview && (
        <div className="code-copy">
          <ButtonPrimary
            id="CopyButtonPrimary"
            onClick={() => CopyWidgetPreviewUrl()}
          >
            {copyWidgetUrl ? "Copied" : `Copy`}
          </ButtonPrimary>
        </div>
      )}
    </WidgetContent>
  );
};

export default MerchantWidgetContainer;
