import styled from "styled-components";

const ProgressBarContainer = styled.div`
  padding-left: 30px;
  width: 190px;
  border-right: 1px solid #ddd;
  padding-right: 10px;
  margin-right: 20px;

  .progress-item {
    padding: 15px 0;
    position: relative;
    &:not(:last-child):before {
      content: "";
      position: absolute;
      height: 100%;
      border: 1px solid #ddd;
      top: 30px;
      left: -20px;
    }
    span {
      align-items: center;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      position: absolute;
      left: -29px;
      display: flex;
      justify-content: center;
    }
    span.pending {
      background: #fff;
      border: 1px solid #a3a3a3;
    }
    span.completed {
      background: #00d48b;
      &:before {
        content: "";
        display: inline-block;
        width: 7px;
        height: 4px;
        border-left: 2px solid #fff;
        border-bottom: 2px solid #fff;
        transform: rotate(312deg);
      }
    }
    span.current {
      background: #00d48b;
      &:before {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #fff;
        transform: rotate(312deg);
      }
    }
  }
  label {
    font-size: 16px;
  }
  p {
    color: #756f82;
    font-size: 12px;
    margin-top: 5px;
  }
`;
const ProgressBarChildContainer = styled.div``;
export default function ProgressBar(props?: any) {
  const urlName = window.location.href.split("/").pop();
  return (
    <ProgressBarContainer className="progressBar">
      <ProgressBarChildContainer className="progress-item">
        <span
          className={checkUrlName(urlName) ? "current" : "completed"}
        ></span>
        <label htmlFor="">Billing Model</label>
        <p>Choose a billing model</p>
      </ProgressBarChildContainer>
      <ProgressBarChildContainer className="progress-item">
        <span className={checkUrlName(urlName) ? "pending" : "current"}></span>
        <label htmlFor="">Setup</label>
        <p>Set price and currency</p>
      </ProgressBarChildContainer>
      <ProgressBarChildContainer className="progress-item">
        <span
          className={checkIntegrationUrlName(urlName) ? "pending" : "current"}
        ></span>
        <label htmlFor="">Integration</label>
        <p>Embed code snippet</p>
      </ProgressBarChildContainer>
    </ProgressBarContainer>
  );
}

function checkUrlName(name: string) {
  return name === "create/choose-billing-model-type" ? true : false;
}

function checkIntegrationUrlName(name: string) {
  return name === "create/widget-preview" ? false : true;
}
