const BtnWrapper = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  left: 0;
  padding: 20px;
  background: #fff;
  border-top: 1px solid #ddd;
  text-align: right;
  button {
    width: 120px;
    height: 40px;
    font-size: 14px;
    border-radius: 10px;
    display: inline-block;
    padding: 5px !important;
  }
`;
const MarginLeft = styled.div`
  margin-left: 0;
`;

export default function CreateWidget() {
  const { t } = useTranslation();
  const { account, chainId } = useActiveWeb3React();
  const { state } = useLocation();

  const history = useHistory();
  const dispatch = useDispatch();

  const selectNavToggle = useContext(NavToggle);

  const [loading, setLoading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);

  const [merchantName, setMerchantName] = useState<string>("");
  const [merchantUrl, setMerchantUrl] = useState<string>("");
  const [reference, setReference] = useState<string>("");

  const [billingCreateType, setBillingCreateType] = useState(null);
  const [updateBilling, setUpdateBilling] = useState(false);
  const [billingModelId, setBillingModelId] = useState(null);
  // single billing
  const [billingName, setBillingName] = useState(null);
  const [billingAmount, setBillingAmount] = useState(null);
  const [billingCurrency, setBillingCurrency] = useState(null);
  // single dynamic
  const [isDynamicBM, setIsDynamicBM] = useState(false);

  // ----------- recurring-billing -----------------
  const [numberOfTrialPeriods, setNumberOfTrialPeriods] = useState<Number>(1);
  const [bmFrequency, setBmFrequency] = useState(1);
  const [bmFrequencyInSeconds, setBmFrequencyInSeconds] = useState(1);

  const [numberOfPayments, setNumberOfPayments] = useState<Number>(null);
  const [noOfDurationPeriods, setNoOfDurationPeriod] = useState(1);
  const [bmFrequencyPeriod, setbmFrequencyPeriod] = useState({
    label: "Hourly",
    periodInSeconds: 3600,
  });
  const [durationPeriod, setPaymentFrequency] = useState({
    label: "Hourly",
    periodInSeconds: 3600,
  });
  const [trialPeriod, setTrialPeriod] = useState({
    label: "Days",
    periodInSeconds: 86400,
  });
  const [initialAmount, setInitialAmount] = useState(1);
  const [trialType, setTrialType] = useState<Number>(1);
  // -------------------------------

  const getBillingModelDetails = () => {
    console.log("need to retrieve missing bm details");
  };

  //check billing details
  useEffect(() => {
    if (state) {
      setIsEdit(state["isEdit"]);
      setBillingName(state["title"]);
      setMerchantName(state["merchantName"]);
      setMerchantUrl(state["merchantUrl"]);
      setReference(state["reference"]);
      setBillingAmount(state["price"]);
      setBillingCurrency(state["currency"]);
      setIsDynamicBM(state["checked"]);
      setNumberOfTrialPeriods(state["numberOfTrialPeriods"]);
      setNoOfDurationPeriod(state["noOfDurationPeriods"]);
      setBmFrequency(state["bmFrequency"]);
      setbmFrequencyPeriod(state["bmFrequencyPeriod"]);
      setPaymentFrequency(state["durationPeriod"]);
      setTrialPeriod(state["trialPeriod"]);
      setInitialAmount(state["initialAmount"]);
      setBillingCreateType(state["billingModelType"]);
      setTrialType(state["trialType"]);
      setUpdateBilling(state["onUpdate"]);
      setBillingModelId(state["billingModelId"]);
      setBmFrequencyInSeconds(
        state["bmFrequencyPeriod"]
          ? state["bmFrequency"] * state["bmFrequencyPeriod"].periodInSeconds
          : null
      );
      setNumberOfPayments(
        state["durationPeriod"]
          ? Math.floor(
              (state["durationPeriod"].periodInSeconds *
                state["noOfDurationPeriods"]) /
                (state["bmFrequency"] *
                  state["bmFrequencyPeriod"].periodInSeconds)
            )
          : null
      );
    }
  }, [state]);

  if (!state) {
    getBillingModelDetails();
  }

  const singleBContract = useSinglePPContract();
  const singleDynamicBContract = useSingleDynamicPPContract();
  const recurringPPContract = useRecurringPPContract();
  const recurringDynamicContract = useDynamicRecurringPPContract();
  const freeTrialContract = useFreeTrialRecurringPPContract();
  const paidTrialContract = usePaidTrialRecurringPPContract();

  const token = useToken(state?.["currency"]["address"]);

  const navigateToBMDetailsPage = (
    bmType: BillingModelTypes,
    txReceipt: TransactionReceipt
  ) => {
    history.push(
      `/merchant/billing-model-details?billing-model-type=${
        BillingModelTypes[bmType]
      }&billing-model-id=${txReceipt["events"][0].args[0].toString()}`
    );
  };

  const handleSuccessfulBlockchainTx = async (
    response: TransactionResponse,
    bmType: BillingModelTypes,
    paymentAmount?: string,
    isUpdate = false
  ) => {
    const summary = `${BillingModelTypeTitles[bmType]} billing model ${
      isUpdate ? "updated" : "created"
    }`;
    const hash = response.hash;
    const bmCreation = {
      tokenAddress: token?.address,
      amount: paymentAmount ? paymentAmount : "Dynamic",
      name: billingName,
      merchantName,
      merchantUrl,
      uniqueReference: reference,
    };
    dispatch(
      addTransaction({
        hash,
        from: account,
        chainId,
        bmCreation,
        summary,
      })
    );
    const txReceipt = await response.wait(WAIT_BLOCK_CONFIRMATIONS);
    setLoading(false);
    navigateToBMDetailsPage(bmType, txReceipt);
  };

  const handleFailedBMCreation = (error, bmType) => {
    setLoading(false);
    console.debug(
      `Failed to create BM for ${bmType}. Reason: ${error.message}`
    );
    throw error;
  };

  const onCreateSingleAndDynamicBilling = async () => {
    if (!state?.["currency"]["address"]) {
      alert("invalid currency address");
      history.push("/merchant/dashboard");
    }
    setLoading(true);
    if (
      BillingModelTypes[billingCreateType] === BillingModelTypes.SingleDynamic
    ) {
      await handleSingleDynamicBMCreation(
        singleDynamicBContract,
        BillingModelTypes.SingleDynamic,
        String(billingAmount)
      );
    } else {
      await handleSingleBMCreation(
        singleBContract,
        BillingModelTypes.Single,
        String(billingAmount)
      );
    }
  };

  const handleSingleDynamicBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes,
    paymentAmount: string
  ) => {
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(account, merchantName, reference, merchantUrl)
      .catch((e) => {
        console.error(e);
        return null;
      });

    return contract
      .createBillingModel(account, merchantName, reference, merchantUrl, {
        gasLimit: calculateGasMargin15(estimatedGas),
      })
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType, paymentAmount);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const handleSingleBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes,
    paymentAmount: string
  ) => {
    setLoading(true);
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address
      )
      .catch((e) => {
        console.error(e);
        return null;
      });
    return contract
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        {
          gasLimit: calculateGasMargin15(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType, paymentAmount);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const handleDynamicRecurringBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes
  ) => {
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(
        account,
        trialType,
        merchantName,
        reference,
        merchantUrl
      )
      .catch((e) => {
        console.error(e);
        return null;
      });

    return contract
      .createBillingModel(
        account,
        trialType,
        merchantName,
        reference,
        merchantUrl,
        {
          gasLimit: calculateGasMargin(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const handleRecurringBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes,
    paymentAmount: string
  ) => {
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        numberOfPayments
      )
      .catch((e) => {
        console.error(e);
        return null;
      });

    return contract
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        numberOfPayments,
        {
          gasLimit: calculateGasMargin(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const handleRecurringFreeTrialBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes,
    paymentAmount: string
  ) => {
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        (
          Number(trialPeriod.periodInSeconds) * Number(numberOfTrialPeriods)
        ).toString(),
        numberOfPayments
      )
      .catch((e) => {
        console.error(e);
        return null;
      });
    return contract
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        (
          Number(trialPeriod.periodInSeconds) * Number(numberOfTrialPeriods)
        ).toString(),
        numberOfPayments,
        {
          gasLimit: calculateGasMargin(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const handleRecurringPaidTrialBMCreation = async (
    contract: Contract,
    bmType: BillingModelTypes,
    paymentAmount: string
  ) => {
    const estimatedGas: BigNumber = await contract.estimateGas
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        (
          Number(trialPeriod.periodInSeconds) * Number(numberOfTrialPeriods)
        ).toString(),
        String(initialAmount),
        numberOfPayments
      )
      .catch((e) => {
        console.error(e);
        return null;
      });
    return contract
      .createBillingModel(
        account,
        billingName,
        merchantName,
        reference,
        merchantUrl,
        paymentAmount,
        billingCurrency?.address,
        bmFrequencyInSeconds,
        (
          Number(trialPeriod.periodInSeconds) * Number(numberOfTrialPeriods)
        ).toString(),
        String(initialAmount),
        numberOfPayments,
        {
          gasLimit: calculateGasMargin(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        setLoading(true);
        await handleSuccessfulBlockchainTx(response, bmType);
      })
      .catch((error: Error) => {
        handleFailedBMCreation(error, bmType);
      });
  };

  const goToPreviousPath = () => {
    if (
      BillingModelTypes[billingCreateType] === BillingModelTypes.Single ||
      BillingModelTypes[billingCreateType] === BillingModelTypes.SingleDynamic
    ) {
      history.push({
        pathname: isEdit
          ? `/merchant/single-billing/edit-billing-model?billing-model-type=${billingCreateType}&billing-model-id=${billingModelId}`
          : "/merchant/create/single-billing-model",
        state: {
          name: billingName,
          amount: billingAmount,
          token: billingCurrency?.address,
          merchantName: merchantName,
          uniqueReference: reference,
          merchantUrl: merchantUrl,
          isDynamic:
            BillingModelTypes[billingCreateType] ===
            BillingModelTypes.SingleDynamic,
        },
      });
    } else {
      history.push({
        pathname: isEdit
          ? `/merchant/recurring-billing/edit-billing-model?billing-model-type=${billingCreateType}&billing-model-id=${billingModelId}`
          : "/merchant/create/recurring-billing-model",
        state: {
          name: billingName,
          merchantName: merchantName,
          uniqueReference: reference,
          merchantUrl: merchantUrl,
          amount: billingAmount,
          token: billingCurrency?.address,
          numberOfTrialPeriods: numberOfTrialPeriods,
          noOfDurationPeriods: noOfDurationPeriods,
          durationPeriod: durationPeriod,
          bmFrequency: bmFrequency,
          bmFrequencyPeriod: bmFrequencyPeriod,
          trialType: trialType,
          initialAmount: initialAmount,
          trialPeriod: trialPeriod,
          isDynamic:
            BillingModelTypes[billingCreateType] ===
            BillingModelTypes.RecurringDynamic,
        },
      });
    }
  };
  const onCreateRecurringAndDynamicBilling = async () => {
    setLoading(true);
    if (
      BillingModelTypes[billingCreateType] ===
      BillingModelTypes.RecurringDynamic
    ) {
      await handleDynamicRecurringBMCreation(
        recurringDynamicContract,
        BillingModelTypes.RecurringDynamic
      );
    } else {
      switch (trialType) {
        case BMTrialTypes.FreeTrial:
          await handleRecurringFreeTrialBMCreation(
            freeTrialContract,
            BillingModelTypes.RecurringFreeTrial,
            String(billingAmount)
          );
          break;
        case BMTrialTypes.PaidTrial:
          await handleRecurringPaidTrialBMCreation(
            paidTrialContract,
            BillingModelTypes.RecurringPaidTrial,
            String(billingAmount)
          );
          break;
        case BMTrialTypes.NoTrial:
        default:
          await handleRecurringBMCreation(
            recurringPPContract,
            BillingModelTypes.Recurring,
            String(billingAmount)
          );
          break;
      }
    }
  };

  const onUpdateSingleBilling = async () => {
    const estimatedGas = await singleBContract.estimateGas.editBillingModel(
      billingModelId,
      account,
      billingName,
      String(billingAmount),
      billingCurrency?.address,
      merchantName,
      merchantUrl
    );
    setLoading(true);
    return singleBContract
      .editBillingModel(
        billingModelId,
        account,
        billingName,
        String(billingAmount),
        billingCurrency?.address,
        merchantName,
        merchantUrl,
        {
          gasLimit: calculateGasMargin15(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        await handleSuccessfulBlockchainTx(
          response,
          BillingModelTypes.Single,
          billingAmount,
          true
        );
      })
      .catch((error: Error) => {
        setLoading(false);
        console.debug("Failed to update billing model. Reason:", error);
        throw error;
      });
  };

  const onUpdateRecurringBilling = async () => {
    let contract;
    switch (trialType) {
      case BMTrialTypes.FreeTrial:
        contract = freeTrialContract;
        break;
      case BMTrialTypes.PaidTrial:
        contract = paidTrialContract;
        break;
      case BMTrialTypes.NoTrial:
        contract = recurringPPContract;
        break;
    }
    const estimatedGas: BigNumber = await contract.estimateGas.editBillingModel(
      billingModelId,
      account,
      billingName,
      merchantName,
      merchantUrl
    );
    setLoading(true);
    return contract
      .editBillingModel(
        billingModelId,
        account,
        billingName,
        merchantName,
        merchantUrl,
        {
          gasLimit: calculateGasMargin(estimatedGas),
        }
      )
      .then(async (response: TransactionResponse) => {
        await handleSuccessfulBlockchainTx(
          response,
          BillingModelTypes[billingCreateType],
          billingAmount,
          true
        );
      })
      .catch((error: Error) => {
        setLoading(false);
        console.debug("Failed to edit billing model. Reason:", error);
        throw error;
      });
  };
  const billingModelDetails = {
    name: billingName,
    merchantName: merchantName,
    merchantUrl: merchantUrl,
    amount: billingAmount,
    token: billingCurrency?.address,
    trialType: trialType,
    numberOfPayments: numberOfPayments ? numberOfPayments : null,
    frequency: bmFrequencyInSeconds ? bmFrequencyInSeconds : null,
    initialAmount: initialAmount,
    trialPeriod: trialPeriod
      ? trialPeriod.periodInSeconds * Number(String(numberOfTrialPeriods))
      : null,
  };

  const handleBMCreation = (e) => {
    e.preventDefault();
    if (isAnySingleBM(BillingModelTypes[billingCreateType])) {
      if (updateBilling) {
        onUpdateSingleBilling()
          .then(async (r) => {})
          .catch((e) => console.error(e));
        return;
      }
      onCreateSingleAndDynamicBilling()
        .then(async (r) => {})
        .catch((e) => console.error(e));
    } else {
      if (updateBilling) {
        onUpdateRecurringBilling()
          .then(async (r) => {})
          .catch((e) => console.error(e));
        return;
      }
      onCreateRecurringAndDynamicBilling()
        .then(async (r) => {})
        .catch((e) => console.error(e));
    }
  };

  return (
    <>
      <MainContent className={selectNavToggle ? " open" : ""}>
        <ProgressBar />
        <div className="arrow">
          <img src={ArrowRight} alt="arrow" onClick={goToPreviousPath} />
        </div>
        <div className="sideContent">
          <MarginLeft>
            <AutoColumn style={{ width: "55%" }} gap="lg" justify="center">
              <InfoCard title={t("preview")} info={t("previewDescription")} />
            </AutoColumn>
          </MarginLeft>
          <MerchantWidgetContainer
            billingModelType={BillingModelTypes[billingCreateType]}
            billingModelDetails={billingModelDetails}
            isPreview={true}
          />
        </div>
        <Box as="form" onSubmit={(e) => handleBMCreation(e)}>
          <BtnWrapper className="btn-wrap" style={{ zIndex: 1 }}>
            <ButtonPrimary
              style={{
                borderColor: "#B2ACBF",
                marginRight: "10px",
                background: "transparent",
                color: "#000",
                width: "130px",
                display: "inline-block",
                padding: "10px",
                borderRadius: "10px",
              }}
              as={Link}
              to="/merchant/create/choose-billing-model-type"
            >{`Cancel`}</ButtonPrimary>
            <ButtonPrimary
              // disabled={!canCreate}
              id="create-bm-button"
              style={{ padding: 16 }}
            >
              {updateBilling ? (
                loading ? (
                  <Loader />
                ) : (
                  "Update"
                )
              ) : loading ? (
                <Loader />
              ) : (
                "Create"
              )}
            </ButtonPrimary>
          </BtnWrapper>
        </Box>
      </MainContent>
    </>
  );
}
